import React, { Component } from 'react';
import classes from './Design.css';



export default class Design extends Component {
  render() {
    return (
      <div className={classes.Design}>
        <span className={classes.Title}>MY<br/> &ensp; DESIGNS</span>
      </div>
    )
  }
}



